import { Button, ButtonProps } from 'reactstrap'

import { mediaDesktop, mediaMobile } from 'common/Responsive'
import { TThemeModel, TThemeModelProps } from 'models/ThemeModel'

import { css, styled } from '../styled'
import { IButtonProps, RouteButton } from './RouteButton'

export const PrimaryButton = styled<ButtonProps>(Button)`
  background-color: ${themeProp('secondary')};
  border-color: ${themeProp('secondary')};
  &:hover {
    background-color: ${themeProp('secondaryHover')};
    border-color: ${themeProp('secondaryHover')};
  }
  &:active {
    background-color: #262626 !important;
  }
`

PrimaryButton.displayName = 'PrimaryButton'

export const SecondaryButton = styled(Button)`
  background-color: ${themeProp('tertiary')};
  border-color: ${themeProp('tertiary')};
  &:hover {
    background-color: ${themeProp('tertiaryHover')};
    border-color: ${themeProp('tertiaryHover')};
  }
`

SecondaryButton.displayName = 'SecondaryButton'

export const PrimaryRoutedButton = styled<IButtonProps>(RouteButton)`
  background-color: ${themeProp('secondary')};
  border-color: ${themeProp('secondary')};
  &:hover {
    background-color: ${themeProp('secondaryHover')};
    border-color: ${themeProp('secondaryHover')};
  }
`

PrimaryRoutedButton.displayName = 'PrimaryRoutedButton'

interface IMobileHeaderProps {
  isConfirm?: boolean
}

export const MobileHeaderButton = styled<IMobileHeaderProps>(Button)`
  background-color: ${({ isConfirm, theme }) =>
    isConfirm ? theme.primary : 'white'};
  color: ${({ isConfirm, theme }) => (isConfirm ? 'white' : theme.primary)};
`

MobileHeaderButton.displayName = 'MobileHeaderButton'

export const SelectButtonThemed = styled<ButtonProps>(Button)`
  ${({ theme, selected }) => css`
    ${mediaDesktop`background-color: ${
      selected ? theme.tertiary : theme.unselected
    }
    `}
    ${mediaMobile`background-color: ${selected ? theme.tertiary : 'white'}`}
    border-color: ${selected ? theme.tertiary : theme.unselected};
    color: ${selected ? theme.tertiaryText : theme.unselectedText};
    height: 3.7rem;
    &:hover {
      background-color: ${
        selected ? theme.tertiaryHover : theme.unselectedHover
      };
      border-color: ${selected ? theme.tertiaryHover : theme.unselectedHover};
      color: ${selected ? theme.tertiaryText : theme.unselectedText};
    }
  `};
`

SelectButtonThemed.displayName = 'SelectButtonThemed'

function themeProp(name: keyof TThemeModelProps) {
  return ({ theme }: { theme?: TThemeModel }) => theme![name]
}
