import { buildQuery, gql } from 'graph'

import { GOrderForm } from 'graph/types'

export const OrderFormQuery = gql`
  query GOrderForm($orderId: ID!) {
    order(id: $orderId) {
      id
      countWares
    }
  }
`

export const QueryOrderForm = buildQuery<
  GOrderForm.Query,
  GOrderForm.Variables
>(OrderFormQuery)
