import { i18n } from 'i18n'
import React from 'react'

import { AlertModal } from '../../common/AlertModal'
import { WithUser } from '../../common/WithUser'

interface IProps {}

export const OrderLimitModal: React.SFC<IProps> = props => (
  <WithUser
    render={user => (
      <AlertModal
        isOpen={!user.orderLimitAlertHidden}
        onClose={user.hideOrderLimit}
        headerText={i18n.t`Limit has been reached`}
      >
        {i18n.t`The maximum number of items in the order has been reached`}
      </AlertModal>
    )}
  />
)
