import { Plural, Trans } from '@lingui/react'
import { i18n } from 'i18n'
import React from 'react'

import { AlertModal } from '../../common/AlertModal'
import { ShopStatus } from '../../common/ShopStatus'
import { WithUser } from '../../common/WithUser'
import { GCompanyBranchStatus } from '../../graph/types'

interface IProps {
  status?: GCompanyBranchStatus.Fragment
}

export const ShopClosingTimeModal: React.SFC<IProps> = ({ status }) =>
  status ? (
    <ShopStatus
      status={status}
      render={({ decision, openingIn }) => (
        <WithUser
          render={user => (
            <AlertModal
              isOpen={
                (decision === 'Closed' || decision === 'OpeningSoon') &&
                !user.closingTimeAlertHidden
              }
              onClose={user.hideClosingTime}
              headerText={i18n.t`We are closed right now`}
            >
              {i18n.t`We're sorry, but we do not accept new orders at this time.`}
              {decision === 'OpeningSoon' && (
                <Trans>
                  You can prepare your order because we are{' '}
                  <Plural
                    value={openingIn}
                    one="opening in 1 minute"
                    other="opening in # minutes"
                  />!
                </Trans>
              )}
            </AlertModal>
          )}
        />
      )}
    />
  ) : null
