import React from 'react'

import { TUserModel } from '../models/UserModel'
import { WithRoot } from './WithRoot'

interface IProps {
  render(root: TUserModel): ReactNode
  observe?: boolean
}

export const WithUser: React.SFC<IProps> = ({ render, observe = true }) => (
  <WithRoot
    observe={observe}
    render={({ initialized, user }) => (initialized ? render(user) : null)}
  />
)
