import React from 'react'
import Scroll from 'react-scroll'
import { styled } from 'styled'

import { mediaMobile } from 'common/Responsive'
import { GFoodMenuCategory } from 'graph/types'

import { FoodInCart } from './types'

interface IProps {
  category: GFoodMenuCategory.Fragment
  foodInCart?: FoodInCart
}

const FoodCategoryStyled = styled('div')`
  color: #474747;
  display: block;
  font-size: 1.6rem;
  & a {
    text-decoration: none;
  }
`

const FoodCategoryTitleStyled = styled('h3')`
  ${mediaMobile`margin: 1rem 0 0.4rem 1rem;`};
`

const FoodCategory: React.SFC<IProps> = ({ category, children }) => (
  <Scroll.Element name={category.nameLabel}>
    <FoodCategoryStyled>
      <FoodCategoryTitleStyled>{category.nameLabel}</FoodCategoryTitleStyled>
      {children}
    </FoodCategoryStyled>
  </Scroll.Element>
)

export default FoodCategory
