import { Trans } from '@lingui/react'
import { i18n } from 'i18n'
import React from 'react'
import { styled } from 'styled'

import { GFinishViewOrder, PaymentGateEnum } from 'graph/types'
import { RM } from 'helpers/ramda'

interface IProps {
  order: GFinishViewOrder.Fragment
}

const HeaderThemed = styled('div')`
  background-color: ${({ theme }) => theme.primary};
`

const PriceThemed = styled('div')`
  background-color: ${({ theme }) => theme.primary};
`

const PriceCornerThemed = styled('div')`
  border-top-color: ${({ theme }) => theme.primary} !important;
`

export const FinishViewOrder: React.SFC<IProps> = ({ order }) => (
  <>
    <HeaderThemed className="finish__view__header">
      <div className="finish__view__header__inner">
        <span className="flaticon-transport-1 finish__view__header__icon" />
        <h3>
          <Trans>It's done! Thank you :-)</Trans>
        </h3>
      </div>
    </HeaderThemed>
    <h4>
      <Trans>Your order was successfully accepted!</Trans>{' '}
      {getPaymentInfo(order)}
    </h4>
    <div className="finish__view__items">
      <div className="finish__view__items__look__to">
        <Trans>You can look forward to:</Trans>
      </div>
      {order.items.map(item => <OrderItem key={item.id} {...item} />)}
      <PriceCornerThemed className="finish__view__items__price__corner" />
      <PriceThemed className="finish__view__items__price">
        {getPaymentPrice(order)} <b>{order.totalSum.formattedValue}</b>
      </PriceThemed>
    </div>
  </>
)

const hasMethod = RM.pathEq(['paymentMethod', 'paymentType', 'enum'])

function getPaymentInfo({ orderPayments }: GFinishViewOrder.Fragment) {
  if (orderPayments.some(hasMethod(PaymentGateEnum.CASH))) {
    return i18n.t`You will pay at pickup.`
  }
  if (orderPayments.some(hasMethod(PaymentGateEnum.GOPAY))) {
    return i18n.t`You have payed.`
  }
  return null
}

function getPaymentPrice({
  orderPayments,
  totalSum,
}: GFinishViewOrder.Fragment) {
  if (orderPayments.some(hasMethod(PaymentGateEnum.CASH))) {
    return i18n.t`Total cost to be payed:`
  }
  if (orderPayments.some(hasMethod(PaymentGateEnum.GOPAY))) {
    return i18n.t`Total cost payed:`
  }
  return null
}

function OrderItem({ recipe, sideDishes }: GFinishViewOrder.Items) {
  return (
    <div>
      <b>{recipe.nameLabel}</b>
      {sideDishes.length
        ? ` + ${sideDishes.map(RM.path(['recipe', 'nameLabel'])).join(',')}`
        : null}
    </div>
  )
}
