import React from 'react'

import { WithRoot } from 'common/WithRoot'

import { CartItemButton } from './CartItemButton'

interface IProps {
  itemId: ID
}

export const RemoveCartItemButton: React.SFC<IProps> = ({ itemId }) => (
  <WithRoot
    render={root => (
      <CartItemButton
        icon="delete"
        color="#99230e"
        data_testid={`removeCartItemButton`}
        onClick={() => root.order.removeItemFromCart(itemId)}
      />
    )}
  />
)
